body{
  font-family: "Helvetica", "Arial", sans-serif;
  line-height: 1.5em;
}

.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #dad4ec 0%, #dad4ec 1%, #f3e7e9 100%);
}

.noResultsFound{
  text-align: center;
  font-style: italic;
  color: #555;
  line-height: 2em;
  font-size: 20px;
}

.search-form{
  position: fixed;
  width: 100vw;
  height: 8vh;
  padding: 1% 0%;
  background-image: radial-gradient(73% 147%, #EADFDF 59%, #ECE2DF 100%), radial-gradient(91% 146%, rgba(255,255,255,0.50) 47%, rgba(0,0,0,0.50) 100%);
  background-blend-mode: screen;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.search-button:hover{
  padding: 9px 19px;
  box-shadow: 1px 1px 1px gray;
}

.recipes{
  padding-top: 7%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}