.Recipe {
    background-image: linear-gradient(to top, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);    border-radius: 10px;
    box-shadow: 0px 1px 1px gray;
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    padding: 50px;
    color: #555
}

.image{
    height: 150px;
    border-radius: 100px;
    margin-bottom: 20px;
}

.title{
    font-weight: 300;
    color: #333;
    line-height: 1.5em;
    text-align: center;
}

.bullet{
    line-height: 2em;
}
